import { createI18n } from 'vue-i18n'
import zh from './locales/zh'
import en from './locales/en'

// è·å–æµè§ˆå™¨è¯­è¨€
const getBrowserLanguage = () => {
  const navigatorLang = navigator.language.toLowerCase()
  if (navigatorLang.includes('zh')) {
    return 'zh'
  }
  return 'en'
}

// è·å–æœ¬åœ°ä¿å­˜çš„è¯­è¨€è®¾ç½®æˆ–ä½¿ç”¨æµè§ˆå™¨è¯­è¨€
const getLocale = () => {
  const cachedLocale = localStorage.getItem('locale')
  if (cachedLocale) {
    return cachedLocale
  }
  return getBrowserLanguage()
}

// ä¿å­˜è¯­è¨€è®¾ç½®åˆ°æœ¬åœ°
export const setLocale = (locale) => {
  localStorage.setItem('locale', locale)
}

// è·å–å½“å‰è®¾ç½®çš„è¯­è¨€
const currentLocale = getLocale()

const i18n = createI18n({
  legacy: false, // ä½¿ç”¨composition APIæ¨¡å¼
  locale: currentLocale,
  fallbackLocale: 'zh', // å¦‚æœæ²¡æœ‰æ‰¾åˆ°ç¿»è¯‘å°±ä½¿ç”¨ä¸­æ–‡
  messages: {
    zh,
    en
  },
  // æ·»åŠ ä¸€äº›é¢å¤–çš„é…ç½®ä»¥ç¡®ä¿æ­£ç¡®åŠ è½½
  silentTranslationWarn: false,
  missingWarn: true,
  fallbackWarn: true
})

export default i18n 